body {
  background: linear-gradient(90deg,  #f1e342 20%, #faa556 100%);
  // background: url('./img/icons/fon2.webp') 0 0/200% 7100px no-repeat;
  // @media(min-width: 1600px){
  //   background: url('./img/icons/fon2.webp') 0 0/100% 7100px no-repeat;
  // }
  font-family:sans-serif;
  letter-spacing: 1px;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
:focus{
  // outline: none;
  //—Ç–∞–±—É–ª—è—Ü—ñ—è –≤–∏–¥–Ω–∞ tab scrole
}
:active {
  outline: none;
}
a:focus{
  // outline: none;
  //—Ç–∞–±—É–ª—è—Ü—ñ—è –≤–∏–¥–Ω–∞ tab scrole
}
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  min-height: 100%;
  width: 100%;
  // height: 100%;
  font-size: 16px;
  font-weight: 400;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;

  .root{
    width: 100%;
     height: 100%; 
  }
}
input,
button,
textarea {
  font-family: inherit;
}
input::-ms-clear {
  display: none;
}
button {
  cursor: pointer;
}
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
a,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
P{
  cursor: default;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 14px;
  font-weight: 400;
  cursor: default;
  //–≤–∏–º–∫–Ω–µ–Ω–Ω—è –∑–º—ñ–Ω–∏ –≤–∏–≥–ª—è–Ω—É –º–∏—à–∫–∏
}